/* fonts */


/*variables */

:root {
  --header-background-image: url();
  --header-background-color: rgba(105, 105, 105, 0.5);
  --header-border-color: rgba(61, 61, 61, 0.9);
  --main-font: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  --button-background-color: rgba(155, 155, 155, 0.7); ;
  --button-background-color-hover: rgba(119, 119, 119, 0.9);;
  --button-background-color-active: rgba(109, 109, 109, 0.9);
  --navbar-selected: rgba(158, 154, 157, 0.9);
  --navbar-hover: rgba(139, 139, 139, 0.9);
  --jumbo-color: rgba(165, 165, 165, 0.0) ;
  --jumbo-color-hover: rgba(133, 133, 133, 0.0) ;
  --range-thumb: rgb(99, 99, 99);
  --range-thumb-width: 15px ;
  --range-thumb-height: 15px ;
  --err-display: none;
}

.bluescreen{
  position:fixed;
  padding:0;
  margin:0;

  top:0;
  left:0;

  width: 100%;
  height: 100%;
  background:#1007b2;
  z-index: 100 !important;
  font-family: "VT323";
  font-size: 1.4em;
  -webkit-font-smoothing : none !important;
  color: white;
  display: none;
  display: var(--err-display);
}

/* general styling */


#align {width: 80%;
margin-left:auto; 
margin-right:auto;
font-family: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
font-family: var(--main-font);
}

/* navbar */

.navimg {  
  position: relative;
  top: 13px;  
}

h1, h2 {
  font-family: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  font-family: var(--main-font);

}

ul.topnav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;  
  border-bottom: 1px solid ;
  border-bottom-color: #eda338;
  font-family: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  font-family: var(--main-font); 
}

ul.topnav li {
  float: left;   
}

ul.topnav li a {
  display: block;
  color: #FFFFFF !important;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a.selected {
  display: block;
  color: #FFFFFF !important;
  background-color: #eda338;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a:hover {
  background-color: #E7205F;
  text-decoration: none;
  color: white;
  transition: all 0.15s ease;
}

ul.topnav li.navright {
  float: right;
  background-color: #03A19A;
}

@media screen and (max-width: 560px) {
  ul.topnav li.navright, 
  ul.topnav li {float: none;}
  .navimg {display: none;}
 
}


/* transitions */

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 150ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active{
  opacity: 0.01;
  transition: opacity 0ms ease-out;
}

div.transition-group {
  position: relative;

}

section.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

/* cards */

.contactCards{
  width: 80%;
