		font-size: 14px;
	}
	
	.ms-ribbon div.ribbon-menu {
		display: none;
	}
}

@media only screen and (max-width: 768px) {
	
	div.ribbon-menu {
		margin-right: .6em;
		border-radius: 3px;
		float: left;
	}
	
	.ms-ribbon-toggle > span.title {
		display: block;
		margin-top: .2em;
	}
	
	div.ribbon-menu > span.line {
		display:block;
		margin:4px 0px 4px 0px;
		height:2px;
		background:#333333;
		width:20px;
	}
	
	label.ms-ribbon-toggle {
		font-family: "Segoe UI" , Verdana, Tahoma, Helvetica, sans-serif;
		font-size: .79em;
		display: block;
		border: 1px solid #b6babf;
		background: #FFF;
		background: -moz-linear-gradient(#ffffff, #eaedf1);
		overflow: hidden;
		padding: 1em;
		cursor: pointer;
	}
	
	.ribbon-content {
		display: none;		
	}
	
	.ms-ribbon {
		font-family: "Segoe UI" , Verdana, Tahoma, Helvetica, sans-serif;
		background: #eaedf1;
		font-size: .75em;
		padding: 0;
		margin: 0;
	}
	
	.ms-ribbon ul {
		/*display: none;*/
		list-style: none;
		padding: 0;
		margin: 0;
	}
	
	.ms-ribbon li {
		padding: 0.7em 1em;
		border-bottom: 1px solid #b6babf;
		border-left: 1px solid #b6babf;
		border-right: 1px solid #b6babf;
		cursor: pointer;
	}
}
