/* fonts */

@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');


/*variables */

:root {
  --header-background-image: url();
  --header-background-color: rgba(105, 105, 105, 0.5);
  --header-border-color: rgba(61, 61, 61, 0.9);
  --main-font: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  --button-background-color: rgba(155, 155, 155, 0.7); ;
  --button-background-color-hover: rgba(119, 119, 119, 0.9);;
  --button-background-color-active: rgba(109, 109, 109, 0.9);
  --navbar-selected: rgba(158, 154, 157, 0.9);
  --navbar-hover: rgba(139, 139, 139, 0.9);
  --jumbo-color: rgba(165, 165, 165, 0.0) ;
  --jumbo-color-hover: rgba(133, 133, 133, 0.0) ;
  --range-thumb: rgb(99, 99, 99);
  --range-thumb-width: 15px ;
  --range-thumb-height: 15px ;
  --err-display: none;
}

.bluescreen{
  position:fixed;
  padding:0;
  margin:0;

  top:0;
  left:0;

  width: 100%;
  height: 100%;
  background:#1007b2;
  z-index: 100 !important;
  font-family: "VT323";
  font-size: 1.4em;
  -webkit-font-smoothing : none !important;
  color: white;
  display: var(--err-display);
}

/* general styling */


#align {width: 80%;
margin-left:auto; 
margin-right:auto;
font-family: var(--main-font);
}

/* navbar */

.navimg {  
  position: relative;
  top: 13px;  
}

h1, h2 {
  font-family: var(--main-font);

}

ul.topnav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;  
  border-bottom: 1px solid ;
  border-bottom-color: #eda338;
  font-family: var(--main-font); 
}

ul.topnav li {
  float: left;   
}

ul.topnav li a {
  display: block;
  color: #FFFFFF !important;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a.selected {
  display: block;
  color: #FFFFFF !important;
  background-color: #eda338;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a:hover {
  background-color: #E7205F;
  text-decoration: none;
  color: white;
  transition: all 0.15s ease;
}

ul.topnav li.navright {
  float: right;
  background-color: #03A19A;
}

@media screen and (max-width: 560px) {
  ul.topnav li.navright, 
  ul.topnav li {float: none;}
  .navimg {display: none;}
 
}


/* transitions */

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 150ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active{
  opacity: 0.01;
  transition: opacity 0ms ease-out;
}

div.transition-group {
  position: relative;

}

section.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

/* cards */

.contactCards{
  width: 80%;
  margin-left:auto; 
  margin-right:auto;
}


/* general */

#flex{
  display: flex;
}

body{
  height: 100%;
  margin: 0;
  background-color: rgba(255, 255, 255, 0.7) !important;
  background-size: cover !important;
  /*background-image: linear-gradient(black, black, #322f31); */
  color: black !important; 
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  text-rendering: optimizeLegibility;
	-webkit-font-feature-settings: "liga", "kern";
}

a{
  color: rgb(68, 68, 68) !important;
}

.heading{
  color: #000000 !important;
  font-weight: 500;  
  text-shadow: -2px 1px rgba(black, 0.1);
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  
}

.card{
  background-color: rgba(145, 145, 145, 0.7) !important;
  z-index: 90;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  
}

.card:hover{
  background-color: rgba(138, 138, 138, 0.7) !important;
  transition: all 0.3s ease;
}

.jumbotron {
  background-color: var(--jumbo-color) !important;
  z-index: 90;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
}

.jumbotron:hover{
  background-color: var(--jumbo-color-hover) !important;
  transition: all 0.3s ease;
}

.form-control {
  background-color: rgba(150, 149, 149, 0.7) !important;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2) !important;
  border: 1px solid rgba(87, 87, 87, 0.9) !important;
}

.form-control-content {
  background-color: rgba(146, 146, 146, 0.9) !important;
}

.custom-control-input:checked ~ .custom-control-label::before {
  background-color: rgba(80, 80, 80, 0.9) !important;
  border: 1px solid rgba(124, 124, 124, 0.5) !important;
}

.custom-range::-webkit-slider-thumb {
  width: var(--range-thumb-width) !important;
  height: var(--range-thumb-height) !important;
  background: var(--range-thumb) !important;
  background-size: contain !important;
}

.custom-range::-moz-range-thumb {
  background-color: rgba(97, 97, 97, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.9) !important;
  width: var(--range-thumb-width) !important;
  height: var(--range-thumb-height) !important;
  background: var(--range-thumb) !important;
  background-size: contain !important;
}

.custom-range::-moz-range-track {
  background-color: rgba(172, 172, 172, 0.9) !important;
  border: 1px solid rgba(124, 124, 124, 0.5) !important;
   }



/* navbar */

.navbar {
  /* background-color: black !important; */  
  background-color: var(--header-background-color);
  background-image: var(--header-background-image);
  list-style-type: none;
  border-bottom: 1px solid ;
  border-bottom-color: var(--header-border-color);
  margin: 0 !important;
  padding: 0px !important;
  overflow: hidden;
  font-family: var(--main-font);
  font-size: large;
  z-index: 100 !important;

}
/*
@media all and (max-width:61.95em){
  .navbar {
    border-bottom-color: black !important;
  
  }
}  */

@media all and (max-width:35.95em){
  .navbar {
    border-bottom-color: black !important;
  
  }
} 

.navbar-brand{
  display: block !important;
  padding: 5px 0px 0px 0px !important;

}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml,<svg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'><path stroke='rgb(237, 163, 56)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/></svg>") !important;
}

.navbar-toggler-icon {  
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    vertical-align: middle;
    content: "";
    background: no-repeat center center;
    background-image: none;
    background-size: auto;
    background-size: 100% 100%;
}


.navbar-toggler {
  background-color: #322f31 !important;
}

.navbar-toggler.collapsed {
  background-color: #322f31 !important;
}



.navbar a {
  display: block;
  color:rgba(150, 150, 150, 0.9)!important;
  text-align: center;
  padding: 14px 16px 16px 16px !important;
  margin-bottom: -2px !important;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}


.navbar a.selected {
  display: block;
  color:rgba(148, 148, 148, 0.9)!important;
  background-color: var(--navbar-selected);
  text-align: center;
  padding: 14px 16px 16px 16px !important;
  margin-bottom: -2px !important;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

.navbar a:hover {
  background-color: var(--navbar-hover);
  text-decoration: none;
  color: white;
  transition: all 0.15s ease;
}

.recharts-text {
  fill: black !important;
  stroke: black;
  stroke-width: 0.03em !important;
}


.recharts-default-tooltip {
  background-color: rgba(168, 168, 168, 0.9) !important;
  border: rgba(50, 47, 49, 0.9) !important;
  border-radius: 5px !important;
}

/* buttons */

.button1 {
  display:inline-block;
  padding:0.2em 1.45em;
  margin:0.1em;
  border:0.15em solid rgba(117, 117, 117, 0.7);
  box-sizing: border-box;
  text-decoration:none;
  font-family: var(--main-font);
  font-weight:400;
  color:#000000;
  background-color: var(--button-background-color);
  text-align:center;
  position:relative;
}


.button1:hover{
  border-color: var(--button-background-color-hover);
  }
.button1:active{
  background-color: var(--button-background-color-active);;
  }
  @media all and (max-width:30em){
   .button1{
  display:block;
  margin:0.2em auto;
  }
} 

.form-check-label {
  font-family: var(--main-font) !important;
}

.form-label {
  font-family: var(--main-font) !important;
}

.spread {
  position: absolute;
  display: block;
  z-index: -1 !important;
}

.front{
  position: relative;
  display: block;
  z-index: 95 !important;
}

.Spreadsheet__cell {
  background-color: rgb(175, 175, 175) !important;
  border-color: rgb(175, 175, 175) !important;
}

.Spreadsheet__custom {
  background-color: rgb(255, 255, 255) !important;
  border-color: rgb(214, 214, 214) !important;
}

.clippy {

  position: fixed;
  right: 0em;
  bottom: 0em;
  display: block;
  z-index: 99 !important;
}

.poppy {

  position: fixed;
  right: 12em;
  bottom: 22em;
  display: block;
  z-index: 99 !important;
}

.popover {
  background-color: #ffffcc !important;
  width: 150px;
  height: 220px;
  z-index: 100 !important;
  border-color: black !important;
}

.tooltip-inner {
  background-color: cadetblue !important;
  border-width: 5px !important;
}

.clippyQuestion {
  cursor: pointer;
}